import React from 'react'
import styled from 'react-emotion'
import Helmet from 'react-helmet'
import EventShowcase from '../components/EventShowcase'
import Footer from '../components/Footer'
import SimpleForm from '../components/SimpleForm'
import Navbar from '../components/Navbar'
import { colors, layout, transitions } from '../theme'

const imgStyle = {
  position: 'absolute',
  left: 0,
  top: 0,
  width: '100%',
  height: '100%',
  zIndex: -1,
}

const StyledFormPage = styled.div`
  .main-container {
    max-width: ${layout.maxWidth};
    margin: auto;

    border-radius: 7px;
    padding: 20px;
    margin: 80px auto;
    margin-bottom: 0;
    display: flex;
  }

  .title {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-right: 70px;

    h1 {
      margin-bottom: 20px;
      color: ${colors.text.normal};
    }
    p {
      font-size: 1.3rem;
      margin-top: 10px;
      color: ${colors.text.light2};
    }
    button {
      color: ${colors.secondary.normal};
      font-weight: 700;
      border: none;
      padding: 0;
      margin-top: 20px;
      font-size: 1.1rem;
      align-self: flex-start;
      cursor: pointer;
      transition: ${transitions.all};
      outline: none;
      background: none;

      &:hover {
        color: ${colors.secondary.light2};
      }
    }
  }

  h3 {
    max-width: ${layout.maxWidth};
    margin: 40px auto;
    padding: 10px 20px;
    text-transform: uppercase;
    font-size: 1.5rem;
    font-weight: 300;
    text-align: center;
    cursor: pointer;
  }

  ul li {
    font-size: 1.5rem;
    margin: 5px 0;
  }

  .form-area {
    flex-shrink: 0;
    flex-basis: 400px;
    align-self: center;
  }

  .bottom-form {
    max-width: ${layout.maxWidth};
    margin: 0 auto;
    margin-bottom: 50px;
    display: flex;
    align-items: center;
    justify-content: center;

    > div {
      flex-basis: 400px;
    }
  }

  @media screen and (max-width: 768px) {
    background-attachment: initial;
    min-height: 400px;
    height: auto;
    .main-container {
      border-radius: 0;
      padding: 0;
      flex-direction: column-reverse;

      .title {
        margin: 25px;
      }

      .title h1 {
        text-align: center;
      }
      .title button {
        display: none;
      }
    }
  }
`

function scrollToExemples() {
  document.getElementById('last-events').scrollIntoView({
    block: 'start',
    behavior: 'smooth',
  })
}

const FormPage = ({ data: { eventShowcaseList } }) => {
  return (
    <StyledFormPage>
      <Helmet>
        <title>
          Obtenez un devis gratuit pour votre banquet • Monbanquet.fr
        </title>
        <meta
          name="description"
          content="Vous organisez un événement ? Obtenez aujourd'hui un devis gratuit pour votre buffet d'entreprise : artisanal, livré à domicile et à partir de 3€/personne."
        />
      </Helmet>
      <Navbar noCta />
      <div className="main-container">
        <div className="title">
          <h1>Grâce à vous, en 2018 nous avons contribué à</h1>
          <ul>
            <li>☕️ booster 304 réunions matinales</li>
            <li>😭 rendre moins triste 27 pots de départ</li>
            <li>🏅 aider 68 équipes à se team builder</li>
            <li>🍻 arroser 250 afterworks</li>
            <li>🎉 faire la fête 497 fois</li>
          </ul>
          <p>
            ... et participé à des dizaines d'autres moments avec l'aide de nos
            artisans
          </p>
          <button onClick={() => scrollToExemples()}>
            Voir quelques exemples →
          </button>
        </div>
        <div className="form-area">
          <SimpleForm />
        </div>
      </div>
      <div>
        <h3
          className="section-title"
          id="last-events"
          onClick={() => scrollToExemples()}
        >
          Quelques événements de l'année 2018
        </h3>
        {eventShowcaseList.events.map((event, index) => (
          <EventShowcase event={event} key={event.id} />
        ))}
      </div>
      <div className="bottom-form">
        <SimpleForm />
      </div>
      <Footer />
    </StyledFormPage>
  )
}

export const query = graphql`
  query OrderQuery {
    eventShowcaseList: contentfulEventShowcaseList(
      id: { eq: "c3Vng8dfF5CIMccySi2Suy8" }
    ) {
      events {
        id
        name
        numberOfPeople
        location
        description {
          description
        }
        pictures {
          id
          description
          resolutions(width: 300, height: 300) {
            ...GatsbyContentfulResolutions_withWebp
          }
        }
        company {
          name
          logo {
            file {
              url
            }
          }
        }
        artisans {
          name
          type
          picture {
            description
            sizes {
              ...GatsbyContentfulSizes_withWebp
            }
          }
        }
      }
    }
  }
`

export default FormPage
export { StyledFormPage }



