import React from 'react'
import styled from 'react-emotion'
import ZoomableImg from '../components/ZoomableImg'
import { borders, colors, layout, shadows } from '../theme'

const StyledEventShowcase = styled.div`
  padding: 30px 0;

  h4 {
    max-width: ${layout.maxWidth};
    margin: 0 auto;
    padding: 10px 20px;
    text-transform: uppercase;
    font-size: 1.6rem;
    font-weight: 900;

    .location {
      color: ${colors.text.light2};
      font-size: 1rem;
      font-weight: normal;
      margin-left: 15px;
      vertical-align: middle;
    }

    &::after {
      content: ' ';
      display: block;
      height: 3px;
      width: 50px;
      background: ${colors.brand.normal};
      border-radius: 50px;
    }
  }

  .carousel {
    display: flex;
    overflow-x: auto;
  }

  .card {
    background: #fff;
    padding: 20px;
    overflow-y: auto;

    p {
      text-align: justify;
    }
    .logo {
      max-height: 100px;
      max-width: 180px;
      display: block;
      margin: auto;
    }
  }

  .card,
  .img {
    box-shadow: ${shadows.both};
    border: 1px solid ${borders.color.normal};
    border-radius: 4px;
    margin: 10px;
    min-width: 300px;
    height: 300px;
    display: flex;
  }

  .card.inverted {
    background-color: ${colors.brand.normal};
    background-image: linear-gradient(
      90deg,
      #c21d36 0,
      #e32b49 50%,
      #c21d36 100%
    );
    color: ${colors.brand.inverted};
  }

  @media (max-width: 768px) {
    .card.summary {
      min-width: 250px;
    }
  }
`

const EventShowcase = ({ event }) => {
  return (
    <StyledEventShowcase>
      <h4>
        {event.name}
        <span className="location">@ {event.location}</span>
      </h4>
      <div className="carousel">
        <div className="card summary">
          <img
            className="logo"
            src={event.company.logo.file.url}
            alt={event.company.name}
          />
          {/* <p className="description">{event.description.description}</p> */}
        </div>
        {event.pictures.map(picture => (
          <ZoomableImg
            resolutions={picture.resolutions}
            alt={picture.description}
            className="img"
            key={picture.id}
          />
        ))}
      </div>
    </StyledEventShowcase>
  )
}

export default EventShowcase
export { StyledEventShowcase }



