import React from 'react'
import styled from 'react-emotion'
import Img from 'gatsby-image'

const StyledZoomableImg = styled.div``

const ZoomableImg = ({ ...props }) => (
  <StyledZoomableImg>
    <Img {...props} />
  </StyledZoomableImg>
)

export default ZoomableImg
export { StyledZoomableImg }



